/* App.css */

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar {
  background-color: #90b6de; /* Background color of the navigation bar */
  padding: 10px;
}

.navbar-nav {
  display: flex;
  justify-content: flex-end; /* Align items to the right */
  list-style-type: none; /* Remove default list style */
  padding-right: 10px; /* Add padding to the right side */
}

.navbar-nav .nav-link {
  padding: 0.5rem 1rem; /* Adjust padding as needed */
  margin-right: 10px; /* Add margin between nav items */
  color: #333; /* Adjust text color as needed */
  text-decoration: none; /* Remove underline */
}

.navbar-nav .nav-link:hover {
  color: #ec2e10; /* Change color on hover */
}

.navbar-nav .nav-link.highlight {
  color: #c7432c; /* Change color for highlighted items */
}

.nav-item {
  border: 1px solid #000; /* Add border style */
  border-radius: 5px; /* Optional: Add border-radius for rounded corners */
  padding: 5px 10px; /* Optional: Adjust padding for better appearance */
}

.nav-item:hover {
  background-color: #f0f0f0; /* Optional: Change background color on hover */
}

.highlight {
  text-decoration: none; /* Optional: Remove underline from links */
  color: #000; /* Optional: Set text color */
}

.navbar-brand {
  display: flex; /* Use flexbox for alignment */
  align-items: center; /* Align items vertically */
  margin-bottom: 20px;
}

.navbar-brand img {
  border-radius: 50%; /* Optional: Apply border-radius for rounded corners */
  margin-right: 10px; /* Optional: Adjust margin between the image and text */
}


