.signup-container {
    max-width: 400px;
    margin: 50px auto;
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    background: linear-gradient(to right, #ff7e5f, #feb47b); /* Gradient background */
  }
  
  .signup-container h1 {
    margin-bottom: 30px;
    text-align: center;
    color: #fff; /* Adjusted text color to be visible on the gradient background */
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  .label {
    display: block;
    margin-bottom: 8px;
    color: #fff; /* Adjusted label color to be visible on the gradient background */
    font-size: 16px;
    text-align: left; 
  }
  
  .input-field {
    width: 100%;
    padding: 12px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 16px;
  }
  
  .submit-btn {
    width: 100%;
    padding: 12px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 25px; /* Increased border-radius for a curved appearance */
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease;
  }
  
  .submit-btn:hover {
    background-color: #0056b3;
  }
  
  